import React from 'react';
import Context from "../../Context";
import { Table } from "antd";

export default class DataTable extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);

    const defaultPagination = {
      current: 1,
      pageSize: props.pageSize || 20,
      total: false,
    };

    this.state = {
      data: [],
      sorter: {},
      loading: false,
      columns: props.columns,
      pagination: defaultPagination,
      sum: 0.00,
      filters: props.filters || {}
    };
  }

  componentDidMount() {
    if (this.props.preserveState) {
      this.restoreState();
    } else {
      this.load();
    }
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (this.props.columns !== this.state.columns) {
      this.setState({ columns: this.props.columns }, () => this.load());
    }

    if (JSON.stringify(this.props.forceSearch) !== JSON.stringify(prevProps.forceSearch)) {
      let { pagination } = this.state;
      pagination.current = 1;
      this.setState({ pagination });

      this.load();
    }

    if (this.props.uuid !== prevProps.uuid) {
      this.load();
    }

    if (this.props.areaId !== prevProps.areaId) {
      this.load();
    }

    if (this.props.operDateIso !== prevProps.operDateIso) {
      this.load();
    }
  }

  makeBasicDefaultOrderParams = () => {
    let result = [];
    if (this.props.columns) {
      this.props.columns.forEach((col) => {
        if (col.defaultSortOrder) {
          result.push(
            `${col.dataIndex} ${
              col.sortDirections === "descend" ? "DESC" : "ASC"
            }`
          );
        }
      });
    }
    return result;
  };

  makeQueryParams = (mode = {}) => {
    let queryParams = {};
    const { pagination, filters, sorter } = this.state;

    queryParams.search = [];

    const filterFields = Object.keys(filters);

    const formatDate = (d) => (d && d.format ? d.format("DD.MM.YYYY") : d);

    filterFields.forEach((key) => {
      if (!filters[key]) return;

      const currentColumn = this.columnGet(key);
      const v = filters[key];

      // 
      if (currentColumn.date) {
        //     /  
        if (v[0]) {
          const unit = currentColumn.date === "week"
            ? "week"
            : currentColumn.date === "month"
              ? "month"
              : "day";

          const from = v[0].startOf(unit).startOf("day");
          const to = v[0].endOf(unit).endOf("day");

          queryParams.search.push({
            field: key,
            compare: "LEQ",
            value: formatDate(from),
          });
          queryParams.search.push({
            field: currentColumn.dateDataIndex ? currentColumn.dateDataIndex : key,
            compare: "GEQ",
            value: formatDate(to),
          });
        }

        return;
      }

      if (currentColumn.period) {
        //      [from, to]
        if (v[1]) {
          const from = v[0]?.startOf("day");
          const to = v[1]?.endOf("day");
          if (from) {
            queryParams.search.push({
              field: key,
              compare: "GEQ",
              value: formatDate(from),
            });
          }
          if (to) {
            queryParams.search.push({
              field: key,
              compare: "LEQ",
              value: formatDate(to),
            });
          }
        }

        return;
      }

      //   (IN)
      if (currentColumn.filterMultiple) {
        if (v[0]) {
          queryParams.search.push({
            field: key,
            valarr: v,
          });
        }
        return;
      }

      // 
      if (currentColumn.text) {
        if (v[0]) {
          queryParams.search.push({
            field: key,
            compare: "IKE",
            value: `%${v[0]}%`,
          });
        }
        return;
      }

      //  
      if (currentColumn.numeric) {
        const min = v[0];
        const max = v[1];

        const hasMin = min !== undefined && min !== null && min !== "";
        const hasMax = max !== undefined && max !== null && max !== "";

        if (hasMin) {
          queryParams.search.push({ field: key, compare: "GEQ", value: String(min) });
        }
        if (hasMax) {
          queryParams.search.push({ field: key, compare: "LEQ", value: String(max) });
        }

        //  :        max,
        //     numericCompare   
        if (!hasMax && !hasMin && v[0] !== undefined) {
          queryParams.search.push({
            field: key,
            compare: currentColumn.numericCompare || "GEQ",
            value: String(v[0]),
          });
        }

        return;
      }

      //  
      if (currentColumn.address) {
        //    { city, settlement, street, house, building, structure, apartment }
        const addr = Array.isArray(v) ? v[0] : v;
        if (!addr) return;

        const addIKE = (fieldName, value) => {
          if (value) {
            queryParams.search.push({
              field: fieldName,
              compare: "IKE",
              value: `%${value}%`,
            });
          }
        };

        const addEQL = (fieldName, value) => {
          if (value !== undefined && value !== null && String(value).trim() !== "") {
            queryParams.search.push({
              field: fieldName,
              compare: "EQL",
              value: String(value).trim(),
            });
          }
        };

        //    contains
        addIKE("city", addr.city);
        addIKE("settlement", addr.settlement);
        addIKE("street", addr.street);

        // / 
        addEQL("house", addr.house);
        addEQL("building", addr.building);
        addEQL("structure", addr.structure);
        addEQL("apartment", addr.apartment);

        return;
      }

      //       (IN)
      queryParams.search.push({
        field: key,
        valarr: v,
      });
    });

    // forceSearch / forceFilter / orderby / pagination   
    if (this.props.forceSearch) {
      queryParams.search = queryParams.search.concat(this.props.forceSearch);
    }

    if (this.props.forceFilter) {
      queryParams.filter = this.props.forceFilter;
    }

    if (sorter.column) {
      queryParams.orderby = [
        `${sorter.field} ${sorter.order === "ascend" ? "ASC" : "DESC"}`,
      ];
    } else if (!sorter.field) {
      queryParams.orderby = this.props.forceOrder || this.makeBasicDefaultOrderParams();
    }

    if (pagination.total && !mode.skipPagination) {
      queryParams.reclimit = pagination.pageSize;
      queryParams.recoffset = (pagination.current - 1) * pagination.pageSize;
    }

    return queryParams;
  };

  columnGet = (key) => {
    let result = {};
    this.state.columns.forEach((col) => {
      if (col.dataIndex === key) {
        result = col;
      }
    });
    return result;
  };

  sum = async () => {
    if (this.state.columns.length === 0) return 0;

    let result = await this.context.api.q(
      this.props.path.replace("/list", "/sum"),
      this.makeQueryParams({ skipPagination: true }),
      false,
      this.props.forceToken || false,
      this.props.skipToken || false
    );

    if (this.props.onSumEnd) {
      this.props.onSumEnd(result.sum);
    }

    return result.sum;
  };

  count = async () => {
    if (this.state.columns.length === 0) return 0;

    let result = await this.context.api.q(
      this.props.path.replace("/list", "/count"),
      this.makeQueryParams({ skipPagination: true }),
      false,
      this.props.forceToken || false,
      this.props.skipToken || false
    );

    if (this.props.onCountEnd) {
      this.props.onCountEnd(result.count);
    }

    return result.count;
  };

  load = async () => {
    let { pagination, columns, sum } = this.state;

    if (columns.length === 0) return;

    let data = [];

    this.loadStart();

    pagination.total = await this.count();

    if (pagination.total) {
      data = await this.context.api.q(
        this.props.path,
        this.makeQueryParams(),
        {
          toArray: true,
        },
        this.props.forceToken || false,
        this.props.skipToken || false
      );
    }

    if (this.props.onSumEnd) {
      sum = await this.sum();
    }

    this.setState({ data, pagination, sum });
    this.loadEnd();
  };

  loadStart = () => {
    this.setState({ loading: true });
    if (this.props.onLoadStart) {
      this.props.onLoadStart();
    }
  };

  loadEnd = () => {
    this.setState({ loading: false });
    if (this.props.onLoadEnd) {
      this.props.onLoadEnd(this.state.data);
    }
  };

  handleTableChange = (pagination, filters, sorter) => {
    if (this.props.preserveState) {
      this.saveState(pagination, filters, sorter);
    }

    if (this.props.onFilter) {
      this.props.onFilter(filters);
    }

    this.setState({ pagination, filters, sorter }, () => this.load());
  };

  saveState = (pagination, filters, sorter) => {
    window.test = { pagination, filters, sorter };
  };

  restoreState = () => {
    let savedState = window.test || {};
    this.setState({ ...savedState }, () => this.load());
  };

  render() {
    const { columns, data, pagination, loading } = this.state;
    return (
      <div>
        <Table
          rowSelection={this.props.rowSelection}
          scroll={{ x: 400 }}
          showSorterTooltip={false}
          columns={columns}
          rowKey={(record) => record.id}
          dataSource={data}
          pagination={pagination}
          loading={loading || this.props.loading}
          onChange={this.handleTableChange}
        />
      </div>
    );
  }
}
